@import '../variables';

/*=============================================================
Top Menu
=============================================================*/
.menu {
    font-family: 'League Spartan Bold', Roboto, sans-serif;
    top: 0;
    left: 0;
    right: 0;
    height: 94px;
    overflow: hidden;
    z-index: 9999;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
    justify-content: space-between;
    -ms-flex-align: center;
    align-items: center;
    -ms-overflow-style: none;
    margin: 0 $page-margin;
    padding-bottom: 4px;

    .title{
        a{
            color: $dark;
            text-shadow: 1px 1px 15px #8d8d8d;
        }
    }

    .nav {
        height: 100%;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-pack: left;
        justify-content: left;
        align-items: center;
        text-shadow: 1px 1px 15px #8d8d8d;

        a {
            color: $dark;
            font-weight: 500;
            font-size: 20px;
            letter-spacing: .05em;
            text-decoration: none;
            margin-right: 25px;

            &:last-child{
                margin-right: 0;
            }
        }
    }
}