@import 'variables';

/*=============================================================
Global
=============================================================*/
* {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}


html {
  line-height: 1.15;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

body {
  font-family: Lato, sans-serif;
  margin: 0px;
  background-color: $light;
  color: $dark;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  font-feature-settings: "pnum";
  font-variant-numeric: proportional-nums;
}

input, textarea{
  font-family: Lato, sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: medium;
  font-family: 'League Spartan Bold', Roboto, sans-serif;
}

a {
  background-color: transparent;
  -webkit-text-decoration-skip: objects;
  text-decoration: none;
}

li,
ol,
ul {
  list-style: none;
  padding: 0;
}

a,
body,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

.btn{
  padding: 10px;
  min-width: 150px;
  background-color: transparent;
  border: 2px solid;
  font-weight: bold;
  border-radius: 4px;
  transition:0.3s;

  &:hover:enabled{
    background-color: #d6d6d6;
    transition:0.3s;
  }

  &:disabled{
    color: #ccc;
    cursor: not-allowed;
  }
}

.input{
  border-radius: 4px;
  border: 1px solid rgba(0,0,0,0.6);
  padding: 10px;
  margin-bottom: 12px;

  &::-webkit-input-placeholder {
    font-size: 14px;
  }

  &::-moz-placeholder {
    font-size: 14px;
  } /* Firefox 19+ */
}


/*=============================================================
Home
=============================================================*/
.home {
  display: flex;
  flex-direction: column;
  height: 100vh;

  .content {
    margin: 0 $page-margin;
    display: flex;
    flex-grow: 4;
    align-items: center;
    justify-content: center;

    .title {
      h1 {
        font-size: 3.5em;
      }

      h2 {
        font-size: 2.5em;
        border-bottom: 4px solid $principal;
      }
    }

    .numProjects {
      color: rgba(0, 0, 0, 0.5);
      display: flex;
      margin-top: -40px;
      margin-left: -40px;

      .numProject {
        color: rgba(0, 0, 0, 0.5);
        margin-right: 70px;
        cursor: pointer;
        font-size: 1.2em;
        position: relative;

        a{
          color: rgba(0, 0, 0, 0.5);
        }

        &:hover {
          color: rgba(0, 0, 0, 1);

          .wmp {
            width: 90px;
            margin-left: -45px;
            margin-top: -45px;
          }

          .numProjectName {
            opacity: 1;
            visibility: visible;
            animation: toUp 0.4s 1;
          }
        }

        .wmp {
          position: absolute;
          width: 80px;
          left: 50%;
          top: 50%;
          margin-left: -40px;
          margin-top: -40px;
          animation: rotation 20s infinite linear;
          -webkit-animation: rotation 20s infinite linear;
          transition: all 0.3s ease;
          -webkit-transition: all 0.3s ease;
        }

        .numProjectName {
          color: $dark;
          font-size: 0.8em;
          position: absolute;
          top: -28px;
          left: 50%;
          margin-left: -50px;
          width: 100px;
          text-align: center;
          visibility: hidden;
          opacity: 0;
          transition: opacity 0.5s ease;
          -webkit-transition: opacity 0.5s ease;
        }
      }
    }
  }
}

@media only screen and (max-width: 1100px) {
  .home {
    .content {
      .title {
        h1 {
          font-size: 2.9em;
        }

        h2 {
          font-size: 2.1em;
        }
      }
    }
  }
}

@media only screen and (max-width: 868px) {
  .home {
    .content {
      margin: 0 $page-marginMob;
      flex-direction: column;

      .title {
        h1 {
          font-size: 3.5em;
        }

        h2 {
          font-size: 2.7em;
          border-bottom: 4px solid #5FF4A4;
        }
      }

      .numProjects {
        margin-top: 60px;
        margin-left: 0;
      }
    }
  }
}

@media only screen and (max-width: 500px) {
  .home {
    .content {

      .title {
        h1 {
          font-size: 2.5em;
        }

        h2 {
          font-size: 1.7em;
        }
      }

      .numProjects {
        font-size: 0.9em;

        .numProject {
          margin-right: 40px;

          &:hover {
            .wmp {
              width: 70px;
              margin-left: -35px;
              margin-top: -35px;
            }
          }

          .wmp {
            width: 60px;
            margin-left: -30px;
            margin-top: -30px;
          }

          .numProjectName {
            visibility: visible;
            opacity: 1;
          }
        }
      }
    }
  }
}

@keyframes rotation {
  from {
    -webkit-transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(359deg);
  }
}

@-webkit-keyframes rotation {
  from {
    -webkit-transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(359deg);
  }
}

@keyframes toUp {
  0% {
    transform: translate(0px, 30px);
  }

  100% {
    transform: translate(0px, 0px);
  }
}

/*=============================================================
Projects
=============================================================*/
.projectsPage {
  .menu {
    border-bottom: 4px solid $principal;
    padding-bottom: 0px;
  }

  .content {
    margin: 0 $page-margin;
    padding-top: 40px;

    .projects {
      padding: 20px 0;
      display: flex;
      flex-wrap: wrap;

      .project {
        width: 24%;
        margin-bottom: 30px;
        margin-right: 1.333%;

        &:nth-child(4n) {
          margin-right: 0;
        }

        .image {
          width: 100%;
          height: 160px;
          border-radius: 4px;
          position: relative;
          padding-top:60%;


          img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            border-radius: 4px;
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
          }
        }

        .title {
          margin-top: 8px;
        }

        .type {
          margin-top: 3px;
        }
      }
    }
  }
}

@media only screen and (min-width: 2500px) {
  .projectsPage {
    .content {
      .projects {
        .project {
          width: 15%;


        }
      }
    }
  }
}

@media only screen and (max-width: 1100px) {
  .projectsPage {
    .content {
      .projects {
        .project {
          width: 32%;
          margin-right: 2%;

          &:nth-child(3n) {
            margin-right: 0;
          }

          &:nth-child(4n) {
            margin-right: 2%;
          }

          .image {
            width: 100%;
            height: 150px;
            background-color: gray;
          }
        }
      }
    }
  }
}

@media only screen and (max-width: 868px) {
  .projectsPage {
    .content {
      .projects {
        .project {
          width: 48%;

          &:nth-child(2n) {
            margin-right: 0;
          }

          &:nth-child(2n-1) {
            margin-right: 4%;
          }
        }
      }
    }
  }
}

@media only screen and (max-width: 500px) {
  .projectsPage {
    .content {
      .projects {
        .project {
          width: 100%;
          margin-right: 0 !important;

          .image {
            height: 200px;
          }
        }
      }
    }
  }
}

  
  /*=============================================================
  Contact
  =============================================================*/
  #contact{
    .menu {
      border-bottom: 4px solid $principal;
      padding-bottom: 0px;
    }
  
    .content{
      display: flex;
      margin: 0 $page-margin;
      padding-top: 40px;
      justify-content: space-between;
  
      .info{
        width: 47%;
      }
  
      .formContainer{
        width: 47%;
  
        form{
          display: flex;
          flex-direction: column;
  
          textarea{
            resize: vertical;
          }
  
          button{
            cursor: pointer;
          }
        }
      }
    }
  }