/*=============================================================
Default Project
=============================================================*/
@import '../variables';

#defaultProject {
    .menu {
      position: absolute;
  
      a {
        color: $light
      }
    }
  
    .headerImg {
      background-color: #343434;
      width: 100vw;
      height: 350px;
  
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  
    .content {
      margin: 0 $page-margin;
  
      .info {
        display: flex;
        padding: 50px 0;
  
        .infoL{
          margin-right: 60px;
          min-width: 300px;
  
          .title{
            display: inline;
            background-color: $principal;
          }
    
          .type{
            font-size: 1.1em;
            margin-top: 10px;
            margin-bottom: 5px;
          }
    
          .date{
            font-size: 1.1em;
          }
        }
  
        .infoR{
          .desc{
            margin-top: 0;
            line-height: 1.4;
            font-size: 16px;
          }
          
          .githubImage{
            width:200px;
            margin-top:10px;
            margin-bottom: 20px;
          }
        }
      }
    }
  }
  
.mainMediaContainer{
    width: 100%;
    position: relative;
    padding-top: 56.25%;
    background-color: #ccc;

    div{
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;

        .video{
            width: 100%;
            height: 100%;
        }
    }

    .gif{
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      width: 100%;
      height: 100%;
    }
}